@import "../../Fonts/typography.css"; 

.home{
    background:#110A0B;
    height:100vh;
    width:100vw;
    display:flex;
    flex-direction:column;
    justify-content:flex-start;
    align-items: center;
}

.home img{
    width:25vw;
    margin-top:30px;
}

.home button{
    text-align:center;
    font-family:'BasquiatRegular';
    font-weight:400;
    font-size:24px !important;
    margin-top:60px;
    background:rgba(0,0,0,0);
    color:#C2A573;
    border:0px;
    padding:10px 5px;
    cursor:pointer;
    margin-left:0px;
}
.home button:focus{
    outline:none;
}
.home button:active{
    outline:none;
}

.home label{
    background:rgba(0,0,0,0);
    color:#C2A573;
    font-family:'Montserrat';
    font-weight:500;
    font-size:18px;
    margin-bottom:20px;
}
.home input{
    text-align:center;
    background:rgba(0,0,0,0);
    border:0px;
    color:#E2E2E2;
    border-bottom:2px solid #C2A573;
    height:30px;
    font-family:'Montserrat';
    font-weight:300;
    font-size:18px;   
}
.home input:focus{
    outline:none;
}
.home input:active{
    outline:none;
}
