@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,600;1,300;1,400;1,600&display=swap);
@font-face {
    font-family: "BasquiatRegular";
    src:  url(/OshunTheGame/static/media/BasquiatRegular.e0a07097.woff2) format('woff2'),
          url(/OshunTheGame/static/media/BasquiatRegular.cf61f9c0.woff) format('woff'),
          url(/OshunTheGame/static/media/BasquiatRegular.b6fb3827.ttf) format('truetype');
}
.home{
    background:#110A0B;
    height:100vh;
    width:100vw;
    display:flex;
    flex-direction:column;
    justify-content:flex-start;
    align-items: center;
}

.home img{
    width:25vw;
    margin-top:30px;
}

.home button{
    text-align:center;
    font-family:'BasquiatRegular';
    font-weight:400;
    font-size:24px !important;
    margin-top:60px;
    background:rgba(0,0,0,0);
    color:#C2A573;
    border:0px;
    padding:10px 5px;
    cursor:pointer;
    margin-left:0px;
}
.home button:focus{
    outline:none;
}
.home button:active{
    outline:none;
}

.home label{
    background:rgba(0,0,0,0);
    color:#C2A573;
    font-family:'Montserrat';
    font-weight:500;
    font-size:18px;
    margin-bottom:20px;
}
.home input{
    text-align:center;
    background:rgba(0,0,0,0);
    border:0px;
    color:#E2E2E2;
    border-bottom:2px solid #C2A573;
    height:30px;
    font-family:'Montserrat';
    font-weight:300;
    font-size:18px;   
}
.home input:focus{
    outline:none;
}
.home input:active{
    outline:none;
}


@media(max-width: 414px){

    .home img{
        width:310px;
        margin-top:10px;
    }

    .home label{
        font-size:16px;
    }
    .home input{
        text-align:center;
        background:rgba(0,0,0,0);
        border:0px;
        color:#E2E2E2;
        border-bottom:2px solid #C2A573;
        height:30px;
        font-family:'Montserrat';
        font-weight:300;
        font-size:16px;   
    }
}
.scene-1{
    width:100vw;
    height:100vh;
    background:#272626;
}
.scene-1 main{
    width:100vw;
    height:100vh;
    background:url(/OshunTheGame/static/media/figuras-homeAsset.f12e2187.png) no-repeat center center;
    background-size:cover;
    display:flex;
    flex-direction: column;
    justify-content: flex-end;
    --animate-duration: 5s;
}

.scene-1-dialog{
    font-family:'BasquiatRegular';
    margin:0px;
    color:#F5E2B0;
    font-size:30px;
}

#dialog-1{
    --animate-duration: 4s;
}
#dialog-2{
    --animate-duration: 4s;
}

.scene-1-textblock{
    text-align: right;
    margin-top:20px;
    margin-bottom:50px;
    margin-right:3%;
}
.scene-1-textblock button{
    text-align:right;
    font-family:'BasquiatRegular';
    font-weight:400;
    font-size:24px;
    margin-top:20px;
    background:rgba(0,0,0,0);
    color:#C2A573;
    border:0px;
    padding:10px 5px;
    cursor:pointer;
}
.scene-1-textblock button:focus{
    outline:none;
}
.scene-1-textblock button:active{
    outline:none;
}



/*ARREGLOS PARA PANTALLAS CORTAS*/

@media(max-height: 760px){
    .scene-1-dialog{
        font-size:24px;
    }
    .scene-1-textblock{
        margin-top:20px;
        margin-bottom:25px;
    }
    .scene-1-textblock button{
        font-size:20px;
        margin-top:5px;
    }
}



@media(max-width: 414px){
    .scene-1 main{
        background:url(/OshunTheGame/static/media/home-mobile-02.219d7479.png) no-repeat center left;
        background-size:cover;
        display:flex;
        flex-direction: column;
        justify-content: flex-start;
    }
    .scene-1-dialog{
        margin-top:15px;
        padding:0px 30px;
        font-size:18px;
    }
    .scene-1-textblock{
        text-align: center;
        margin-top:100px;
        margin-bottom:25px;
    }
    .scene-1-textblock button{
        text-align:center;
        font-size:22px !important;
        padding-top:25px;
        margin:auto;
    }
}
.scene-2{
    width:100vw;
    height:100vh;
    background:#110A0B;
}
.scene-2 main{
    width:100vw;
    height:100vh;
    background:url(/OshunTheGame/static/media/interior-desktop.f764f5c4.png) no-repeat center center;
    background-size:cover;
    display:flex;
    flex-direction: column;
    justify-content: flex-end;
    --animate-duration: 5s;
    position:relative;
}

.oshun-scene-2{
    position:absolute;
    bottom:0;
    right:12vw;
    z-index:20;
}

.oshun-scene-2 img{
    width:35vw;
}

.diosa-scene-2{
    position:absolute;
    top:0;
    right:12vw;
    z-index:10;
    animation-name:floating;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    animation-direction: alternate;
    animation-duration: 2s;
    animation-play-state: running;
}

.diosa-scene-2 img{
    width:10vw;
}

.oshunposeida-scene-2{
    position:absolute;
    bottom:10px;
    right:30px;
    z-index:10;
    --animate-duration: 2s;
}

.oshunposeida-scene-2 img{
    width:55vw;
}


.scene-2-textblock-user img{
    position:absolute;
    bottom:50px;
    left:50px;
    width:41vw;
    opacity:0.8;
}

.scene-2-textblock-user .answer-user{
    position:absolute;
    top:65vh;
    left:5vw;
    text-align:left;
    width:45vw;
    margin:auto;
}

.diosa-dialog{
    color:#CD4C3F !important;
}

button{
    text-align:left;
    font-family:'BasquiatRegular';
    font-weight:400;
    font-size:1.2em;
    margin-top:20px;
    margin-left:10px;
    background:rgba(0,0,0,0);
    color:#110A0B;
    border:0px;
    width:35vw;
    cursor:pointer;
}
button:focus{
    outline:none;
}
button:hover{
    outline:none;
    color:#7F6740;
}
button:active{
    outline:none;
}

.scene-2-textblock-oshun{
    position:absolute;
    top:13vh;
    left:33vw;
    text-align:center;
    width:25vw;
    font-family:'BasquiatRegular';
    margin:auto;
    color:#F5E2B0;
    font-size:1.5em;
}

.popup-pocima{
    position:fixed;
    top:10vh;
    left:30vw;
    z-index:30;
    background:#959595;
    padding:30px;
    height:auto;
    width:40%;
    text-align:center;
    vertical-align: auto;
    --animate-duration: 1.5s;
box-shadow: 0px 10px 24px 3px rgba(0,0,0,0.75);
}

.popup-pocima h5{
    color:#fff;
    font-size:1.5em;
    font-family: 'BasquiatRegular';
}
.popup-pocima img{
    position:relative;
    top:0px;
    left:0px;
    width:8vw;
    margin:15px 0px;
}
.popup-pocima p{
    color:#fff;
    font-size:1em;
    padding:20px 55px;
    font-family: 'Montserrat';
    font-weight:400;
}
.popup-pocima button{
    text-align:center;
    margin:0px;
}


#futureInput{
    width:70%;
    height:10vh;
    padding:15px 8px;
    text-align:left;
    background:rgba(0,0,0,0);
    border:0px;
    border-bottom:1px solid rgba(17, 10, 11, 0.2);
    color:#110A0B;
    margin:0px 0px 15px 20px;
    height:30px;
    font-family:'Montserrat';
    font-weight:300;
    font-size:18px;   
}
#futureInput:focus{
    outline:none;
}
#futureInput:active{
    outline:none;
}




@media(max-width: 1420px){
    .scene-2-textblock-user .answer-user{
        top:60vh;
    }
    .scene-2-textblock-oshun{
        font-size:1em;
    }
    button{
        margin-left:15px;
        font-size:0.8em !important;
    }
    .popup-pocima img{
        width:5vw;
    }
    .popup-pocima p{
        font-size:0.8em;;
    }
    .popup-pocima button{
        font-size:2em;
    }
    .popup-pocima h5{
        font-size:1.2em;
    }
}   



@keyframes floating {
    0%{
        top:70px;
    }
    100%{
        top:30px;
    }
}

