@import "../../Fonts/typography.css"; 

.scene-2{
    width:100vw;
    height:100vh;
    background:#110A0B;
}
.scene-2 main{
    width:100vw;
    height:100vh;
    background:url('../../Images/interior-desktop.png') no-repeat center center;
    background-size:cover;
    display:flex;
    flex-direction: column;
    justify-content: flex-end;
    --animate-duration: 5s;
    position:relative;
}

.oshun-scene-2{
    position:absolute;
    bottom:0;
    right:12vw;
    z-index:20;
}

.oshun-scene-2 img{
    width:35vw;
}

.diosa-scene-2{
    position:absolute;
    top:0;
    right:12vw;
    z-index:10;
    animation-name:floating;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    animation-direction: alternate;
    animation-duration: 2s;
    animation-play-state: running;
}

.diosa-scene-2 img{
    width:10vw;
}

.oshunposeida-scene-2{
    position:absolute;
    bottom:10px;
    right:30px;
    z-index:10;
    --animate-duration: 2s;
}

.oshunposeida-scene-2 img{
    width:55vw;
}


.scene-2-textblock-user img{
    position:absolute;
    bottom:50px;
    left:50px;
    width:41vw;
    opacity:0.8;
}

.scene-2-textblock-user .answer-user{
    position:absolute;
    top:65vh;
    left:5vw;
    text-align:left;
    width:45vw;
    margin:auto;
}

.diosa-dialog{
    color:#CD4C3F !important;
}

button{
    text-align:left;
    font-family:'BasquiatRegular';
    font-weight:400;
    font-size:1.2em;
    margin-top:20px;
    margin-left:10px;
    background:rgba(0,0,0,0);
    color:#110A0B;
    border:0px;
    width:35vw;
    cursor:pointer;
}
button:focus{
    outline:none;
}
button:hover{
    outline:none;
    color:#7F6740;
}
button:active{
    outline:none;
}

.scene-2-textblock-oshun{
    position:absolute;
    top:13vh;
    left:33vw;
    text-align:center;
    width:25vw;
    font-family:'BasquiatRegular';
    margin:auto;
    color:#F5E2B0;
    font-size:1.5em;
}

.popup-pocima{
    position:fixed;
    top:10vh;
    left:30vw;
    z-index:30;
    background:#959595;
    padding:30px;
    height:auto;
    width:40%;
    text-align:center;
    vertical-align: auto;
    --animate-duration: 1.5s;
    -webkit-box-shadow: 0px 10px 24px 3px rgba(0,0,0,0.75);
-moz-box-shadow: 0px 10px 24px 3px rgba(0,0,0,0.75);
box-shadow: 0px 10px 24px 3px rgba(0,0,0,0.75);
}

.popup-pocima h5{
    color:#fff;
    font-size:1.5em;
    font-family: 'BasquiatRegular';
}
.popup-pocima img{
    position:relative;
    top:0px;
    left:0px;
    width:8vw;
    margin:15px 0px;
}
.popup-pocima p{
    color:#fff;
    font-size:1em;
    padding:20px 55px;
    font-family: 'Montserrat';
    font-weight:400;
}
.popup-pocima button{
    text-align:center;
    margin:0px;
}


#futureInput{
    width:70%;
    height:10vh;
    padding:15px 8px;
    text-align:left;
    background:rgba(0,0,0,0);
    border:0px;
    border-bottom:1px solid rgba(17, 10, 11, 0.2);
    color:#110A0B;
    margin:0px 0px 15px 20px;
    height:30px;
    font-family:'Montserrat';
    font-weight:300;
    font-size:18px;   
}
#futureInput:focus{
    outline:none;
}
#futureInput:active{
    outline:none;
}




@media(max-width: 1420px){
    .scene-2-textblock-user .answer-user{
        top:60vh;
    }
    .scene-2-textblock-oshun{
        font-size:1em;
    }
    button{
        margin-left:15px;
        font-size:0.8em !important;
    }
    .popup-pocima img{
        width:5vw;
    }
    .popup-pocima p{
        font-size:0.8em;;
    }
    .popup-pocima button{
        font-size:2em;
    }
    .popup-pocima h5{
        font-size:1.2em;
    }
}   



@keyframes floating {
    0%{
        top:70px;
    }
    100%{
        top:30px;
    }
}
